<template>
  <div class="GoBoard">
    <div v-for="i in 15" :key="i" class="row">
      <Cell
        v-for="j in 15"
        :key="j"
        :x="i"
        :y="j"
        :ref="i + '-' + j"
        :hoverStatus="color"
      />
    </div>
  </div>
</template>

<script>
import Cell from "./Cell.vue";
export default {
  name: "GoBoard",
  components: {
    Cell,
  },
  data() {
    return {
      color: 0,
    };
  },
  mounted() {
    window.socket.on("position", (data) => {
      let target = data.x + "-" + data.y;
      this.$refs[target][0].setStatus(data.status);
    });
    window.socket.on("color", (data) => {
      if (data > 0) this.color = data;
    });
  },
  methods: {},
};
</script>

