<template>
  <main>
    <NavBar />
    <div class="center">
      <div id="Login">
        <img src="@/assets/gogame.jpg" />
        <form @submit.prevent="sendForm()">
          <input type="text" v-model="nickname" placeholder="Nickname" /><br />
          <input type="submit" value="GO" />
        </form>
      </div>
      <span>{{ errMsg }}</span>
    </div>
  </main>
</template>

<script>
import NavBar from "@/components/NavBar";
export default {
  name: "Login",
  components: { NavBar },
  data() {
    return {
      nickname: "",
      errMsg: "",
    };
  },
  created() {
    window.socket.removeAllListeners();
  },
  mounted() {
    window.socket.close();
    window.socket.open();

    if (window.nickname) this.nickname = window.nickname;
    window.socket.on("err", (errCode) => {
      if (errCode == 0) {
        window.nickname = this.nickname;
        this.$router.push("/gogame");
      }
      if (errCode == 1) this.errMsg = "Invalid nickname";
      if (errCode == 2) this.errMsg = "The nickname has been used";
    });
  },
  methods: {
    sendForm() {
      window.socket.emit("nickname", this.nickname);
    },
  },
};
</script>

