<template>
  <main>
    <NavBar />
    <div id="GoGame">
      <h1 v-if="opponent">Your opponent is {{ opponent }}</h1>
      <h1 v-if="errMsg">
        {{ errMsg }}<br />Return to the previous page in 1 second
      </h1>
      <h1 v-else-if="gameOverText">Game Over : {{ gameOverText }}</h1>
      <h1 v-else-if="round == color && color != 0 && round != 0">Your Round</h1>
      <h1 v-else-if="round != color && color != 0 && round != 0">
        Your opponent's Round
      </h1>
      <h1 v-else>Waiting for new player...</h1>
      <GoBoard />
    </div>
  </main>
</template>

<script>
import NavBar from "@/components/NavBar";
import GoBoard from "@/components/GoBoard.vue";
export default {
  name: "GoGame",
  components: {
    GoBoard,
    NavBar,
  },
  data() {
    return {
      round: 0,
      gameOverText: "",
      errMsg: "",
      opponent: "",
      color: 0,
    };
  },
  created() {
    window.socket.removeAllListeners();
  },
  mounted() {
    window.socket.on("gameOver", (gameOverCode) => {
      console.log(gameOverCode, this.gameOverText);
      if (gameOverCode == -1 && this.gameOverText == "")
        this.gameOverText = "Your opponent left, you win";
      else if (gameOverCode == this.color) this.gameOverText = "You Win";
      else this.gameOverText = "You Lose";
    });
    window.socket.on("round", (round) => {
      this.round = round;
    });
    window.socket.on("err", (errCode) => {
      if (errCode == 1) this.errMsg = "Invalid nickname";
      else if (errCode == 2) this.errMsg = "The nickname has been used";
      if (errCode)
        setTimeout(() => {
          this.$router.push("/");
        }, 1000);
    });
    window.socket.on("nickname", (opponent) => {
      this.opponent = opponent;
    });

    window.socket.on("color", (data) => {
      if (data > 0) this.color = data;
    });
    window.socket.emit("ready", window.nickname);
  },
};
</script>

