<template>
  <nav>
    <img src="@/assets/banner.jpg" @click="clickIcon()" />
    <div id="menu">
      <span v-if="again">Again</span>
      <span>Nickname : {{ nickname }}</span>
    </div>
  </nav>
</template>

<script>
export default {
  name: "NavBar",
  props: { again: Number },
  data() {
    return {
      nickname: "None",
    };
  },
  mounted() {
    if (window.nickname) this.nickname = window.nickname;
  },
  methods: {
    clickIcon() {
      if (this.$router.currentRoute.name != "Login") this.$router.push("/");
    },
  },
};
</script>
<style scoped>
nav {
  height: 7vh;
  width: 100%;
  background: rgb(24, 188, 156);
  position: relative;
  top: 0;
  display: flex;
  justify-content: space-between;
}
nav img {
  height: 100%;
}

#menu {
  display: flex;
  align-items: center;
}
#menu span {
  color: white;
  font-size: 2.5em;
  margin-right: 0.8em;
}

nav {
  font-size: 1vh;
}
</style>
