import Vue from "vue";
import Router from "vue-router";
import Login from "@/Pages/Login";
import GoGame from "@/Pages/GoGame";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      redirect: {
        name: "Login",
      },
    },
    {
      path: "/login",
      name: "Login",
      component: Login,
    },
    {
      path: "/gogame",
      name: "GoGame",
      component: GoGame,
    },
  ],
});
